import { useState } from "react";

import "./Skills.scss";

import { ChevronLeft, ChevronRight, GitHub } from "@material-ui/icons";

export default function Works({ darkActive }) {
  const [currentSlider, setCurrentSlider] = useState(0);

  const handleClick = (direction) => {
    if (direction === "left") {
      if (currentSlider > 0) {
        setCurrentSlider(currentSlider - 1);
      } else {
        setCurrentSlider(`${data.length - 1}`);
      }
    } else {
      if (currentSlider < `${data.length - 1}`) {
        setCurrentSlider(currentSlider + 1);
      } else {
        setCurrentSlider(0);
      }
    }
  };

  const data = [
    {
      id: "1",
      title: "Programming Languages",
      desc1:
        "Full algorithmic programming proficiency in Java and full modern JavaScript knowledge (ECMA and ES6)",
      link1: "https://www.hackerrank.com/payasnikhurpa?hr_r=1",
      link1_desc: "HackerRank Profile",
      link2: "https://www.codechef.com/users/payas_2001",
      link2_desc: "CodeChef Profile",
      img: process.env.PUBLIC_URL + "/assets/img1.png",
    },
    {
      id: "2",
      title: "MERN STACK",
      desc1:
        "Strong knowledge of JavaScript ,CSS & Sass, React.Js, node.js, MongoDB and familiar with in CSS frameworks like Bootstrap and Bulma",
      img_link: "https://github.com/MagnuS-Taken",
      img: process.env.PUBLIC_URL + "/assets/img2.jpg",
    },
    {
      id: "3",
      icon: "./assets/writing.png",
      title: "Additional Skills",
      desc1:
        "Knowledge of Operating Systems and Database Management Systems (MySQL)",
      desc2: "Version Control Systems: Git & Github",
      img: process.env.PUBLIC_URL + "/assets/img3.jpg",
    },
  ];

  return (
    <div className={"skills " + (darkActive && " dark")} id="skills">
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlider * 100}vw)` }}
      >
        {data.map((d) => {
          return (
            <div key={d.id} className="container">
              <div className="item">
                <div className="left">
                  <div className="leftContainer">
                    <h2>{d.title}</h2>
                    <p>{d.desc1}</p>
                    <p>{d.desc2}</p>
                    {d.img_link === undefined ? (
                      <p></p>
                    ) : (
                      <a href={d.img_link} target="_blank" rel="noreferrer">
                        <GitHub />
                        Link to GitHub
                      </a>
                    )}
                    {d.link1 === undefined ? (
                      <p></p>
                    ) : (
                      <a href={d.link1} target="_blank" rel="noreferrer">
                        {d.link1_desc}
                      </a>
                    )}
                    {d.link1 === undefined ? (
                      <p></p>
                    ) : (
                      <a href={d.link2} target="_blank" rel="noreferrer">
                        {d.link2_desc}
                      </a>
                    )}
                  </div>
                </div>
                <div className="right">
                  <img src={d.img} alt={d.title} />
                </div>
              </div>
            </div>
          );
        })}
      </div>
      <ChevronLeft className="arrow left" onClick={() => handleClick("left")} />
      <ChevronRight
        className="arrow right"
        onClick={() => handleClick("right")}
      />
    </div>
  );
}
