import { useState, useEffect } from "react";

import "./Project.scss";
import "./ProjectList.scss";
import {
  featuredPortfolio,
  webPortfolio,
  mobilePortfolio,
  contentPortfolio,
} from "../../data";

export default function Project({ darkActive }) {
  const list = [
    {
      id: "featured",
      title: "E-commerce App",
    },
    {
      id: "fitness",
      title: "Fitness App",
    },
    {
      id: "cowin",
      title: "Cowin Notification App",
    },
  ];

  const [selected, setSelected] = useState("featured");
  const [data, setData] = useState([]);

  useEffect(() => {
    switch (selected) {
      case "featured":
        setData(featuredPortfolio);
        break;
      case "fitness":
        setData(webPortfolio);
        break;
      case "cowin":
        setData(mobilePortfolio);
        break;
      default:
        setData(contentPortfolio);
    }
  }, [selected]);

  return (
    <div id="project" className={"project " + (darkActive && " dark")}>
      <h1>Projects</h1>
      <ul>
        {list.map((item) => {
          return (
            <li
              key={item.id}
              className={
                "projectList " +
                (selected === item.id
                  ? "projectList active "
                  : "projectList ") +
                (darkActive && " dark")
              }
              onClick={() => setSelected(item.id)}
            >
              {item.title}
            </li>
          );
        })}
      </ul>
      <div className="container">
        {data.map((data) => {
          return (
            <div key={data.id} className="item">
              <img src={data.img} alt={data.title} />
            </div>
          );
        })}
      </div>
    </div>
  );
}
