import { useState } from "react";
import { ToggleOff, ToggleOn } from "@material-ui/icons";

import Topbar from "./components/topbar/Topbar";
import Menu from "./components/menu/Menu";
import Intro from "./components/intro/Intro";
import Project from "./components/project/Project";
import Skills from "./components/skills/Skills";
import Certificates from "./components/certificates/Certificates";

import "./App.scss";

function App() {
  const [menuState, setMenuState] = useState(false);
  const [isDarkModeActive, setDarkModeActive] = useState(false);

  const switchMode = () => {
    console.log(isDarkModeActive + " ->" + !isDarkModeActive);
    setDarkModeActive(!isDarkModeActive);
  };

  return (
    <div className="app">
      <div className="toggle" onClick={() => switchMode()}>
        {isDarkModeActive ? (
          <ToggleOn className="on" />
        ) : (
          <ToggleOff className="off" />
        )}
      </div>
      <Topbar
        menuState={menuState}
        setMenuState={setMenuState}
        darkActive={isDarkModeActive}
      />
      <Menu
        menuState={menuState}
        setMenuState={setMenuState}
        darkActive={isDarkModeActive}
      />
      <div className="sections">
        <Intro darkActive={isDarkModeActive} />
        <Skills darkActive={isDarkModeActive} />
        <Project darkActive={isDarkModeActive} />
        <Certificates darkActive={isDarkModeActive} />
      </div>
    </div>
  );
}

export default App;
