import { useEffect, useRef } from "react";

import { ExpandMore } from "@material-ui/icons";
import { init } from "ityped";

import "./Intro.scss";

export default function Intro({ darkActive }) {
  const textRef = useRef();

  useEffect(() => {
    init(textRef.current, {
      showCursor: true,
      backDelay: 1500,
      backSpeed: 60,
      strings: ["Front End Developer", "Java Developer"],
    });
  }, []);

  return (
    <div className={darkActive ? "intro dark" : "intro"} id="intro">
      <div className="left">
        <div className="imgContainer">
          <img src={process.env.PUBLIC_URL + "/assets/image.png"} alt="" />
        </div>
      </div>
      <div className="right">
        <div className="imgContainer">
          <div className="wrapper">
            <h2>Hi There,I'm</h2>
            <h1>PAYAS SINGH NIKHURPA</h1>
            <h1 className="text-effect">
              <span ref={textRef}></span>
            </h1>
            <a href="#portfolio" aria-label="go-below">
              <ExpandMore className="icon" />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
