import { useState } from "react";

import "./Certificates.scss";

import { ChevronLeft, ChevronRight } from "@material-ui/icons";

export default function Certificates({ darkActive }) {
  const [currentSlider, setCurrentSlider] = useState(0);

  const handleClick = (direction) => {
    if (direction === "left") {
      if (currentSlider > 0) {
        setCurrentSlider(currentSlider - 1);
      } else {
        setCurrentSlider(`${data.length - 1}`);
      }
    } else {
      if (currentSlider < `${data.length - 1}`) {
        setCurrentSlider(currentSlider + 1);
      } else {
        setCurrentSlider(0);
      }
    }
  };

  const data = [
    {
      id: "1",
      title: "Data Structures",
      link1: "https://coursera.org/share/e2defb87a8dd2cbcc193284b4511e135",
      img: process.env.PUBLIC_URL + "/assets/c1.jpg",
    },
    {
      id: "2",
      title: "Database Management Essentials",
      link1: "https://coursera.org/share/048e2a904fa2f76014573e1f6e66356f",
      img: process.env.PUBLIC_URL + "/assets/c2.jpg",
    },
    {
      id: "3",
      title: "Introduction to Web Development",
      link1: "https://coursera.org/share/3f8a73fc3dbdf711b5102f11e6761b7d",
      img: process.env.PUBLIC_URL + "/assets/c3.jpg",
    },
    {
      id: "4",
      title: "Java (Basic) Certificate",
      link1: "https://www.hackerrank.com/certificates/e2b0e25f486f",
      img: process.env.PUBLIC_URL + "/assets/c4.jpg",
    },
    {
      id: "5",
      title: "JavaScript (Basic) Certificate",
      link1: "https://www.hackerrank.com/certificates/3930d7c77718",
      img: process.env.PUBLIC_URL + "/assets/c5.jpg",
    },
  ];

  return (
    <div
      className={"certificates " + (darkActive && " dark")}
      id="certificates"
    >
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlider * 100}vw)` }}
      >
        {data.map((d) => {
          return (
            <div key={d.id} className="container">
              <div className="item">
                <div className="left">
                  <div className="leftContainer">
                    <h2>{d.title}</h2>
                    <a href={d.link1} target="_blank" rel="noreferrer">
                      Certificate Link
                    </a>
                  </div>
                </div>
                <div className="right">
                  <img src={d.img} alt={d.title} />
                </div>
              </div>
            </div>
          );
        })}
      </div>
      <ChevronLeft className="arrow left" onClick={() => handleClick("left")} />
      <ChevronRight
        className="arrow right"
        onClick={() => handleClick("right")}
      />
    </div>
  );
}
