import "./Menu.scss";

export default function Menu({ menuState, setMenuState, darkActive }) {
  const collapseMenu = () => {
    setMenuState(!menuState);
  };

  return (
    <div
      className={"menu " + (menuState && "active ") + (darkActive && " dark")}
    >
      <ul>
        <li onClick={() => collapseMenu()}>
          <a href="#intro">Home</a>
        </li>
        <li onClick={() => collapseMenu()}>
          <a href="#skills">Skills</a>
        </li>
        <li onClick={() => collapseMenu()}>
          <a href="#project">Projects</a>
        </li>
        <li onClick={() => collapseMenu()}>
          <a href="#certificates">Certificates</a>
        </li>
      </ul>
    </div>
  );
}
